import React from 'react';
import Page from '../components/layout/page';
import { IndexHero, IndexAboutMe, IndexPosts } from '../components/page-content/index-content';


const IndexPage = () => {
  return (
    <>
      <Page active="index">
          <IndexHero />
          <IndexAboutMe />
          <IndexPosts />
      </Page>
    </>
  )
}

export default IndexPage;
