import React from "react";
import { LayoutContainer, LayoutRow, LayoutItemWrapper, LayoutItem } from '../layout/layout';
import { Heading, Text } from '../base/typography';
import ButtonLink from '../base/links/button-link';
import SvgWrapper from "../base/svgwrapper";

//svgs
import IndexWelcome from '../../images/svg/index-welcome.svg';
import IndexProfile from '../../images/svg/index-profile.svg';
import IndexHire from '../../images/svg/index-hire.svg';
import IndexDevelopFaster from '../../images/svg/index-developfaster.svg';
import ScrollHint from "../base/scroll-hint";

const IndexHero = () => {
    return (
        <>
        <LayoutContainer>
            <LayoutRow center reverse>
                    <LayoutItemWrapper height={'50vw'} center overflowHidden>
                        <LayoutItem maxWidth>
                            <Heading level={1}>I like building.</Heading>
                            <Text>OutSystems applications to be specific.</Text>
                            <ButtonLink to="/work">See my work now</ButtonLink>
                        </LayoutItem>
                    </LayoutItemWrapper>
                    <LayoutItemWrapper height={'50vw'} center primaryColor>
                        <SvgWrapper>
                            <IndexWelcome />
                        </SvgWrapper>
                    </LayoutItemWrapper>
                    <ScrollHint />
                </LayoutRow>
        </LayoutContainer>
        </>
    )
};

const IndexAboutMe = () => {
    return (
        <>
        <LayoutContainer>
            <LayoutRow center>
                    <LayoutItemWrapper rowGap center>
                            <SvgWrapper small>
                                <IndexProfile />
                            </SvgWrapper>
                            <Heading level={3} center>Creative Low Code developer from Rotterdam</Heading>
                            <ButtonLink to="/about">Get to know me</ButtonLink>
                    </LayoutItemWrapper>
                </LayoutRow>
        </LayoutContainer>
        </>
    )
};

const IndexPosts = () => {
    return (
        <>
        <LayoutContainer>
            <LayoutRow reverse center>
                    <LayoutItemWrapper center>
                        <LayoutItem maxWidth>
                            <Heading level={3}>Open for an opportunity</Heading>
                            <Text>Want to lift your company's app to a new level? I'm offering a fresh mind with a focus on usability, continuity and design.</Text>
                            <ButtonLink to="/contact">Hire me now</ButtonLink>
                        </LayoutItem>
                    </LayoutItemWrapper>
                    <LayoutItemWrapper center>
                        <SvgWrapper medium>
                            <IndexHire />
                        </SvgWrapper>
                    </LayoutItemWrapper>
            </LayoutRow>
            <LayoutRow>
                <LayoutItemWrapper center>
                    <SvgWrapper medium>
                        <IndexDevelopFaster />
                    </SvgWrapper>
                </LayoutItemWrapper>
                <LayoutItemWrapper center>
                    <LayoutItem maxWidth>
                        <Heading level={3}>Develop faster</Heading>
                        <Text>Low Code allows to speed up your development cycles. Put less effort in re-inventing the wheel and more in functionality that really matters.</Text>
                    </LayoutItem>
                </LayoutItemWrapper>
            </LayoutRow>
        </LayoutContainer>
        </>
    )
};

export { IndexHero, IndexAboutMe, IndexPosts };